import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { YeuCauSuDungPTNModel } from 'src/app/shared/models/yeu-cau-su-dung-ptn.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { LichSuSuDungPTNService } from './lich-su-su-dung-ptn.service';
import { UserModel } from 'src/app/shared/models/user.model';
import { environment, eMenuClient } from 'src/environments/environment';
import { MainNavComponent } from '../../main.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

@Component({
  selector: 'app-lich-su-su-dung-ptn',
  templateUrl: './lich-su-su-dung-ptn.component.html',
  styleUrls: ['./lich-su-su-dung-ptn.component.scss'],
})
export class LichSuSuDungPTNComponent implements OnInit {
  _today = new Date();
  _length = 0;
  displayedColumns: string[] = [
    'TenLoaiHocVien',
    'TenDeTai',
    'TenGVHD',
    'ThoiGian',
    'TenNoiQuanLy',
    'TenHVCH_NCS',
    'MaHVCH_NCS',
  ];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    public __dialog: MatDialog,
    private __service: LichSuSuDungPTNService,
    private __mainComponent: MainNavComponent,
    private __snackbar: MatSnackBar,
    private __router: Router
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.CLIENT,
      eMenuClient[eMenuClient.LICH_SU_SU_DUNG_PTN]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.CLIENT
    );
  }
  _yeuCauSuDungPTNModels: YeuCauSuDungPTNModel[];
  __user: UserModel;
  dataSource: MatTableDataSource<YeuCauSuDungPTNModel>;
  showProgressBar = false;
  ngOnInit(): void {
    this.showProgressBar = true;
    this.getDatas();
  }
  delay(ms: number) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }
  private getDatas() {
    this.showProgressBar = true;
    this.__service.getAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._yeuCauSuDungPTNModels = data;
        this._yeuCauSuDungPTNModels.forEach((f) => {
          if (new Date(f.ThoiGianKetThuc).getTime() < new Date().getTime())
            f.IsOver = true;
        });
        this.dataSource = new MatTableDataSource(this._yeuCauSuDungPTNModels);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        // this.dataSource.filterPredicate =
        //   (data: LichSuMuonThietBiModel, filter: string) => (
        //     data.MaThietBi.toLocaleLowerCase().indexOf(filter) != -1
        //     || data.HoTenSV.toLocaleLowerCase().indexOf(filter) != -1
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
